import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@vue/page/Home'
import About from '@vue/page/About'
import Contact from '@vue/page/Contact'
import Repair from '@vue/page/Repair'
import NotFound from '@vue/page/NotFound'
import Privacy from '@vue/page/Privacy'
import Gallery from '@vue/page/Gallery'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      title: '   - ',
      layout: 'home'
    }
  },
  {
    path: '/about',
    name: 'about',
    component: About,
    meta: {
      title: '   -  ',
    }
  },
  {
    path: '/gallery',
    name: 'gallery',
    component: Gallery,
    meta: {
      title: '   - ',
    }
  },
  {
    path: '/car-service',
    name: 'car-service',
    component: Repair,
    meta: {
      title: '   - ',
    }
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact,
    meta: {
      title: '   - ',
    }
  },
  {
    path: '/privacy',
    name: 'privacy',
    component: Privacy,
    meta: {
      title: '   -      ',
    }
  },
  // {
  //   path: '/vehicles',
  //   name: 'vehicles',
  //   component: Contact,
  //   meta: {
  //     title: 'Contact page',
  //   }
  // },
  {
    path: '/404',
    name: '404',
    component: NotFound,
    meta: {
      title: '   -   ',
    }
  },
  {
    path: '*',
    redirect: '/404',
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "exact-active",
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
})

export default router