: 112px 0 0;\n\n  $root: &;\n\n  @media(max-width: 767px) {\n    padding: 50px 0 80px;\n    min-height: auto;\n  }\n\n  &::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: #000;\n    opacity: .4;\n  }\n\n  &__box {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n  &__content {\n    max-width: 411px;\n    margin-left: auto;\n    position: relative;\n    z-index: 1;\n  }\n\n  &__title {\n    color: #FFF;\n    margin-bottom: 31px;\n\n    @media(max-width: 767px) {\n      font-size: 30px;\n      line-height: 36px;\n    }\n\n    @media(max-width: 479px) {\n      font-size: 26px;\n      line-height: 32px;\n    }\n\n    @media(max-width: 410px) {\n      font-size: 24px;\n      line-height: 30px;\n    }\n\n  }\n  &__button {\n    width: 86px;\n    height: 86px;\n    background: #FFF;\n    border-radius: 999px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    animation: pulse 3s linear infinite;\n    margin-left: 27px;\n    cursor: pointer;\n    transition: background .3s ease;\n    z-index: 3;\n\n    &:hover, &:focus, &:active {\n      background: #f1f1f1;\n    }\n\n    &_stop {\n      position: absolute;\n      margin: 0;\n    }\n\n    @media(max-width: 767px) {\n      width: 70px;\n      height: 70px;\n    }\n\n    @media(max-width: 479px) {\n      width: 65px;\n      height: 65px;\n    }\n  }\n\n  &__button-ico {\n    width: 22px;\n    height: 28px;\n    margin-left: 6px;\n    color: $red-color;\n\n    @media(max-width: 767px) {\n      width: 20px;\n      height: 26px;\n      margin-left: 5px;\n    }\n    @media(max-width: 479px) {\n      width: 16px;\n      height: 22px;\n      margin-left: 4px;\n    }\n  }\n\n  &__wrap {\n    width: 100%;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: all .3s ease;\n\n    &_stop {\n      &::before {\n    